<template>
  <div class="box" :style="{left:x+'rem', top:y+'rem', width:w+'rem',height:h+'rem'}">
    <div>{{tip}}</div>
  </div>
</template>

<script>
export default {
  name: 'box',
  props: ['tip','x','y','w','h'],
  data () {
    return {
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
.box {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.32rem;
  color: #E89529;
  background: rgba(210, 200, 160, 0.7);
}
</style>



